.search-form {
  height: auto;
  // background: $color-purple-dark;
  // border: 2px solid $color-purple-dark;
  border-radius: 3px;
  clear: both;
  line-height: 2.2rem;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    top: 6px;
    right: 6px;
    width: calc(2.2rem - 12px);
    height: calc(2.2rem - 12px);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    background-image: url('../images/icon-search.svg');
    pointer-events: none;
  }

  label {
    float: left;
    width: calc(100% - 35px);
  }

  input.search-field {
    height: 2.2rem;
    margin: 0;
    padding: 0 10px;
    border: 0 !important;
    border-radius: 3px;
    box-sizing: border-box;
    width: 100%;
  }

  input.search-submit {
    display: block;
    height: 2.2rem;
    width: 2.2rem;
    // color: $color-purple-dark;
    text-indent: -100em;
    overflow: hidden;
    border: none;
    // background: $color-purple-dark;
    cursor: pointer;
  }
}

// Sign up Contact Form
#ctct-form-286 {
  .ctct-form-field {
    text-align: center;
    margin: 0;
  }

  .ctct-submit {
    width: 100%;
  }

  label {
    font-size: 0.7rem;
    color: $color-white;
  }

  input {
    border: 1px solid $color-white;
    width: 99.5%;
  }
}



